import React from 'react'
import styled from 'styled-components'
import { BaseLayout, Heading, Card, CardBody } from 'uikit'
import Page from 'components/layout/Page'

const Hero = styled.div`
  align-items: center;
  background-image: url('/images/logo.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin: auto;
  margin-bottom: 32px;
  padding-top: 116px;
  text-align: center;
  min-height: 200px;
`

const Cards = styled(BaseLayout)`
  align-items: stretch;
  justify-content: stretch;
  margin-bottom: 48px;

  & > div {
    grid-column: span 6;
    width: 100%;
  }

  ${({ theme }) => theme.mediaQueries.sm} {
    & > div {
      grid-column: span 8;
    }
  }

  ${({ theme }) => theme.mediaQueries.lg} {
    & > div {
      grid-column: span 6;
    }
  }
`

const StyledCard = styled(Card)`
  min-height: 376px;
`

const Manual: React.FC = () => {
  return (
    <Page>
      <Hero />
      <div>
        <Cards>
          <StyledCard>
            <CardBody>
              <Heading size="lg" mb="24px">
                Agent Manual
              </Heading>
              <p>Can you keep a secret? I do not think so. Hello Agent, and welcome to TopSecret Finance.</p>
              <br />
              <p>This is a sligh explanation about this project:</p>
              <br />
              <ul>
                <li>Collect several access tokens (AXXX) using data leak token (C101)</li>
                <li>Each layer unlocks new features</li>
                <li>Liquidity layer (A009) and Yield layer (A010) are up and running</li>
                <li>Supra Dollar (aka A011) will be used in combination with the other tokens</li>
              </ul>
              <br />
              <p>Farm and immerse yourself in the fictional world of Secret Agents and Top Secret files.</p>
              <br />
              <Heading size="lg" mb="24px">
                Agencies
              </Heading>
              <p>
                Now you can choose between two agencies: <b>BSC</b> (codename MI5) or <b>FANTOM</b> (codename GHOST).
              </p>
              <br />
              <p> An additional agency is running... you need to find it.</p>
            </CardBody>
          </StyledCard>

          <StyledCard>
            <CardBody>
              <Heading size="lg" mb="24px">
                Tokenomics
              </Heading>
              <Heading size="md" mb="8px">
                DATA LEAK TOKEN (C101)
              </Heading>
              <p>Limited Supply token. Can be bought or farmed with partner projects.</p>
              <br />
              <Heading size="md" mb="8px">
                LIQUIDITY LAYER (A009)
              </Heading>
              <p>Farm and provide liquidity to A009. New layer requires previous layer liquidity.</p>
              <br />
              <Heading size="md" mb="8px">
                YIELD LAYER (A010)
              </Heading>
              <p>Provide liquidity C101-A009 and get A010, you can revert after 7 days.</p>
              <br />
              <Heading size="md" mb="8px">
                ??? LAYER (A011)
              </Heading>
              <p>A011 mission was dropped to implement Supra Dollar (aka SpyDollar).</p>
              <br />
            </CardBody>
          </StyledCard>
        </Cards>
      </div>
    </Page>
  )
}

export default Manual
